<p class="header-padding2">In the summer of 2018, I was driving, when I had an idea. I’d been in the “vegan” sphere for a while, and noticed that there were many what I would call “extreme” vegans. I’m not talking about the people who do public awareness campaigns or vegan activists. I am talking about the people who, block their non-vegan friends on Facebook, who get mad and disconnect from those who aren’t vegan. There is some understanding that I have about why these people do this. It’s like people who don’t talk to Trump Supporters because of their political views. But at the same time, it doesn’t make sense to me. Why ignore and block these people, when education is so pivotal. </p>
<p>During this time, I had a few friends reach out to me about being vegan. I sent them many texts and gave them a lot of information.</p>
<p>During this drive, it struck me, there aren’t many great areas for vegans and non-vegans to talk. There isn’t a place that accepts people and teaches them. </p>
<h2 class="header-padding"><a class="font-deco">I had a sudden idea, to create a podcast. With the goal to help transition people towards veganism, to allow non-vegans to learn bits about veganism but not be hostile.</a></h2>
<b-img rounded class="image-size" :src="veganambition_about" img-alt="This is a screenshot of the Vegan Ambition About Us page."></b-img>
<p class="header-padding2">By the next week, I was sitting in my friend's apartment with a recording device, asking him about <a target="_blank" class="link" href="https://open.spotify.com/episode/3SqdXFllWtCCiXbYg77UOc">Twizzlers and whether honey is vegan.</a> </p>
<p>As of November 2nd, 2018, I’ve released 16 episodes, I’ve recorded 20, my Instagram has 2700 followers, my podcast has been listened to over 1500 times, and I’ve helped to teach people about veganism. During the summer, I designed and coded a <a target="_blank" class="link" href="https://theveganambition.com">website</a> to help reach more people (it needs a re-design - since I changed my style). I’m growing, and I’m doing what is important to change the world. </p>
<h2 class="header-padding"><a class="font-deco">I don’t pressure people who aren’t vegan, I try to educate them.</a> </h2>
<p>This podcast has taught me a lot. I’ve learned the importance of <a target="_blank" class="link" href="https://www.instagram.com/theveganambition/">Social Media,</a> how to schedule posts and create batches, and I’ve learned how to use hashtags. I’ve learned how to create a podcast, edit it, host it, and do all the background work. I’ve figured out how to normalize and make audio sound better. I still have a lot to learn, but this has been one of the most interesting enterprises of my life and I’m excited to see where it will take me. </p>
