      <b-container class="middle-margin">
   <h2 class="header-padding"><a class="font-deco">All of a sudden, we found something that broke us but gave us hope.</a></h2>
      </b-container>
      <b-container class="middle-margin header-padding">
        <p>We found a video that gave us the information we had missed, that this product wouldn’t work for our target market. People with mild ASD could reorganize their schedules. So we asked ourselves, what do we do now? We contacted the person that made the video. After researching with the video creator and continuing our research we found a new problem. </p>
<p>People with ASD need schedules at work but rarely get them. Detailed, specific, and thought out schedules. Managers can sometimes spend up to 6 hours a day managing their employees with ASD. </p>
      </b-container>
