  <h2 class="header-padding"><a class="font-deco">"In the last 5 years, I've learned how to be empathetic, how to understand the world, and how to help others."</a></h2>
<p>This semester we have a capstone project. My team and I decided to pick what we think was the hardest prompt which was to design a solution to help people with Hidden Disabilities. We ended up picking Autism Spectrum Disorder as the hidden disability to focus on. It has been an incredible learning journey. Going into this project, I barely knew the scope of what ASD contains and how it affects people. I’ve learned so much and there is so much still to learn. We’re working on designing a solution to a problem that people encounter right now. In most of my project’s I’ve been the team lead or project manager, and I’m doing that again for this project. </p>
</b-container>
  <b-container class="middle-margin">
    <h4><span class="heading5">Third Year</span></h4>
    <p>The third year was a wild ride for me. In our first semester, I worked on a group project to design a product called Mental Health Indigenous. This was a website designed to help Indigenous people who live in northern Ontario with their mental health. We designed a website where people could learn mental health skills, talk to professionals, talk to friends and learn more about their cultures. </p>
<p>In the third year, I also joined a group on campus called Fashion For Change and I was a teaching assistant for a class we took in the first year. That semester I learned more about coding, more about working with others and I got to start re-designing a real website. I also learned a lot about interacting with people that are younger than you and teaching them skills.</p>
<p>In the winter of that year, I went on exchange to Hong Kong. That was incredible. It was so great to be part of a different culture, a different way of life and meet so many different people. I had always loved traveling but this was a step up from traveling. Getting to live in such a bustling city was remarkable and traveling to different countries in Asia was so much fun. I got to go to South Korea, Taiwan, Vietnam, Singapore, China, and Macau. </p>
<p>In the summer of that year, I worked for a start-up called SnapSuite based in Toronto. There was a large focus on learning to code at this position. My manager wanted me to learn VueJS and apply it to several projects. On top of this, I was creating video content, documents and more. I had two main projects one was a coding project to take a design already done and add VueJS to it. This was a learning curve for me, to say the least. The other was a design project. It was to design a portal for companies to have customers log in and create service calls and billing. This was a summer of learning. </p>
<p>That summer I also started a podcast which was a completely new venture for me. Since starting I’ve hit over 2500 Instagram followers, 1500 listens and I've interviewed 20 people! It’s been so much fun to do the social media, organizing, and creating podcasts.</p>
