  <h4><span class="heading5">Second Year</span></h4>
    <p>When I returned to Waterloo I had a new found knowledge of Design Thinking (my team taught this at Stanford). And the understanding that I definitely did not want to do film anymore. Most of my second year is a blur for me, I know we learned coding and business. But I don’t know if we made any projects. </p>
    <p>After my second year, I knew I wanted to be a UX Designer. But with my background, it was hard to get a UX job. So I reached out to my contacts on LinkedIn and SAP and was able to find a position at SAP Waterloo. Throughout my summer there, I learned more and more about UX. We did a couple of projects there, I worked on a video for SAP’s conference that was played by Hasso Plattner (yes that one). It was only 30 seconds long but it was the most stressful week I’ve ever had.</p>
    <h2 class="header-padding"><a class="font-deco">My most memorable quote from someone was “I’ve worked here for 30 years and I’ve never got that kind of recognition” after my clip played for all SAP to see.</a></h2>
    <p> For half the summer we worked on two main projects. One was a game to test our predictive analytics system and another was a movie picker for the Toronto International Film Festival. We had a lot of trouble getting both of these done. TIFF was very slow with getting us the information we needed, and the other project was very difficult in scope (we had to do a lot of testing to make it work). My manager had also given me the task of developing the front-end of both of these websites which for me (someone who barely knew how to code) was a difficult task. That summer I learned a couple of things. I learned how to code HTML and CSS, I learned that projects can sometimes be out of your hands and I learned that Waterloo is a little boring in the summer.</p>
 <h4><span class="heading5">First year in GBDA</span></h4>
    <p>In my first year of Global Business and Digital Arts, I started a <a target="_blank" class="link" href="https://www.youtube.com/channel/UC9kg3RLnPdV2UBgenstYHDg">YouTube channel</a> with <a target="_blank" class="link" href="https://www.instagram.com/danielbrenneman/">@danielbrenneman</a> who is now one of my best friends. We explored comedy, and filmmaking, in a way I have never done before. We would write a script, take a day to shoot, and 3 days to edit. It was hectic and fun.</p>
    <p>In the summer of my first year, I was trying to find an internship, I somehow stumbled upon a video making role at SAP in Palo Alto. I’m not sure what I said or did, but they offered me a job and 3 weeks later I flew out to California. To say I learned a lot that summer would be an understatement. It was my first time working at a big corporate company and sitting at a desk. But I did some cool work, in my 3 months there. I had to organize, book, and shoot 19 interviews with students from a class that my co-workers taught at Stanford University. I learned how to write better emails (something I struggled with for a while), I learned how to turn over videos quickly and how to take criticism and feedback.</p>
    <iframe class="image-padding" width="100%" height="500" src="https://www.youtube.com/embed/1kBdk-aKTro" frameborder="0" allow="autoplay; encrypted-media" allowfullscreen></iframe>
    <p class="header-padding">I also learned how to live in a different city by myself for the first time. I had an almost 2-hour commute while living there, it was a killer. I somehow managed to win a 4K TV from a competition at an Intel conference. And I had a meeting with the CEO of Scoop Technologies. I met people working at Google, Twitter, Facebook. I worked alongside the Chief Design Officer of SAP and many other high-ranking employees. Someone told me a rumor that our building was Apple's first headquarters (not a confirmed rumor). To think I was working where Steve Jobs may have spent hours working - that was awesome. </p>
   <h4><span class="heading5">First program at UW</span></h4>
    <p>I first went to UW for either Theatre or Film (you decide your major after the first year). I wanted to be behind the stage or behind the camera. I learned a lot about acting, about the stage, and about the art of cinema. That summer, I went on a trip to Israel through Birthright. I don’t want to be one of those people that say that this was an “eye-opening” experience, but it was. I wasn’t satisfied with being a theatre major or being a director. I wanted to do something else, something bigger, something that I felt could change the world and make it better. </p>
    <p>So I switched programs. I have spent the last 4 years learning how to design, how to empathize, how to understand business, and how to see the big picture. </p>
