import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import snapsuite from '@/components/snapsuite'
import about from '@/components/about'
import fashionforchange from '@/components/fashionforchange'
import veganambition from '@/components/veganambition'
import glide from '@/components/glide'

Vue.use(Router)

export default new Router({
  scrollBehavior () {
    return { x: 0, y: 0 }
  },
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/snapsuite',
      name: 'snapsuite',
      component: snapsuite
    },
    {
      path: '/about',
      name: 'about',
      component: about
    },
    {
      path: '/fashionforchange',
      name: 'fashionforchange',
      component: fashionforchange
    },
    {
      path: '/veganambition',
      name: 'veganambition',
      component: veganambition
    },
    {
      path: '/glide',
      name: 'glide',
      component: glide
    }
  ]
})



